body {
  text-align: center;
  font-family: sans-serif;

  background:pink;
  height: auto;
  background-attachment: fixed;
  background-size: cover;
}

ul {
  list-style: none;
}

a {
  display: block;
  font-style: none;
  text-decoration: none;
}

h1,
h2,
h3,
ul,
p {
  margin: 0;
  padding: 0;
}

img {
  display: block;
  max-width: 100%;
  height: auto;
}